.post {
    font-size: 18px;
    color: rgb(49, 67, 81);
    .post-banner {
        h1 {
            margin: 0px;
            border-bottom: 2px solid transparent;
            transition: border 0.2s ease 0s;
            font-size: 52px;
            line-height: 1.2;
            letter-spacing: -0.01em;
            font-weight: 500;
            font-family: Graphik, Helvetica, Arial, sans-serif;
            padding: 80px 0;
            -webkit-font-smoothing: auto;
        }
    }

    h1,
    h2,
    h3 {
        line-height: 1.2;
        letter-spacing: -0.01em;
        font-weight: 500;
        margin: 1.8em 0px 0.8em;
    }

    h1 {
        font-size: 1.8rem;
    }
    h2 {
        font-size: 1.6rem;
    }

    h3 {
        font-size: 1.4rem;
    }

    p {
        line-height: 1.8;
        letter-spacing: -0.005em;
        color: rgb(49, 67, 81);
    }

    ol,
    ul {
        padding: revert;
        margin: revert;
    }

    blockquote {
        border-left: 3px solid rgb(29, 43, 54);
        margin: 35px 0px;
        padding: 5px 0px 5px 18px;
        background: #cccccc3d;
    }

    .bio {
        display: flex;
        color: rgba(29, 43, 54, 0.9);
        letter-spacing: -0.005em;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .info {
            display: flex;
            flex-direction: column;
            padding: 30px 0;
        }

        img {
            width: 40px;
            border-radius: 40px;
            border: 1px solid #ccc;
            margin-right: 10px;
        }
        .info {
            display: flex;

            .author {
                font-size: 1rem;
                line-height: 1.2;
                display: block;
                font-weight: bold;
            }

            .date {
                font-size: 13px;
                line-height: 1.8;
                display: block;
                font-style: italic;
                color: #495961;
            }
        }
    }
}

.posts {
    .card {
        h1 {
            font-size: 2rem;;
        }
    }
    .card.grid {
        justify-content: space-between;
        grid-template-columns: 60px auto;
        justify-content: flex-start;
    }

    .fab {
        color: var(--dark-color);
    }
}
